body,
h1 {
  margin: 0;
}

body {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(./assets/images/back.jpg);
  background-attachment: fixed;
  color: #fff;
  font-family: 'Square Peg', cursive;
}

.item-enter {
  opacity: 0;
}
.item-enter-active {
  opacity: 1;
  transition: all 200ms ease-in;
}
.item-exit {
  opacity: 1;
}
.item-exit-active {
  opacity: 0;
  transition: all 200ms ease-in;
}
