.wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
  transition: 0.3s all;
}

.modal_window {
  width: 100%;
  max-width: 320px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 30;
  box-shadow: 0 3px 10px -0.5px rgba(0, 0, 0, 0.2);
  text-align: center;
  padding: 30px;
  border-radius: 3px;
  background-color: #bdbdbd;
  transition: 0.3s all;
}

.input {
  border: 1px solid #000;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -khtml-border-radius: 3px;
  background: #bdbdbd;
  outline: none;
  width: 244px;
  padding: 10px 5px;
  color: #fff;
  font-size: 25px;
  font-family: 'Square Peg', cursive;
}

.button {
  padding: 6px 10px;
  font-size: 30px;
  background-color: #ffbf52;
  font-family: 'Square Peg', cursive;
}
