.add_item {
  max-width: 800px;
  margin: auto;
  padding: 0 40px;
  position: fixed;
  top: 80%;
  left: 85%;
}

.button {
  background-color: grey;
  border: 1px solid #000;
  border-radius: 100%;
  cursor: pointer;
  color: #fff;
  display: block;
  font: normal 45px arial;
  height: 50px;
  line-height: 49px;
  margin: 20px;
  text-align: center;
  text-decoration: none;
  width: 50px;
}
