.list_todo {
  max-width: 800px;
  margin: auto;
  padding: 30px 40px;
  background-color: #bdbdbd;
  border-radius: 9px;
  margin-bottom: 55px;
  transition: height 0.3s ease;
  position: relative;
}

.nothing {
  font-size: 35px;
  text-align: center;
}

.header {
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 40px;
}

.ring {
  background-image: url(../../assets/images/Ring.png);
  top: -69px;
  left: 0;
  width: 100%;
  height: 122px;
  position: absolute;
}
